import React from 'react';

// Here we are using object destructuring assignment to pluck off our variables from the props object
// We assign them to their own variable names
function NavTabs({ currentPage, handlePageChange }) {
  return (
    <div className="nav-top fixed flex flex-nowrap lg:flex-start items-center z-20 top-0 left-0 right-0 overflow-y-auto max-h-screen lg:overflow-visible lg:max-h-full bg-[#060024]">
      <nav className="container mx-auto px-4 xl:max-w-6xl ">
        <ul className="flex flex-row justify-between py-.5">
          <li className="relative">
            <a
              href="#home"
              onClick={() => handlePageChange("Home")}
              // This is a conditional (ternary) operator that checks to see if the current page is "Home"
              // If it is, we set the current page to 'nav-link-active', otherwise we set it to 'nav-link'
              className={
                currentPage === "Home"
                  ? "block py-3 px-4 text-[#4ac2ed] hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
                  : "block py-3 px-4 text-slate-100 hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
              }
            >
              Home
            </a>
          </li>

          <li className="relative">
            <a
              href="#projects"
              onClick={() => handlePageChange("Projects")}
              // Check to see if the currentPage is `Projects`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
              className={
                currentPage === "Projects"
                  ? "block py-3 px-4 text-[#4ac2ed] hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
                  : "block py-3 px-4 text-slate-100 hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
              }
            >
              Projects
            </a>
          </li>
          <li className="relative">
            <a
              href="#contact"
              onClick={() => handlePageChange("Contact")}
              // Check to see if the currentPage is `Contact`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
              className={
                currentPage === "Contact"
                  ? "block py-3 px-4 text-[#4ac2ed] hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
                  : "block py-3 px-4 text-slate-100 hover:text-[#4ac2ed] focus:text-[#4ac2ed]"
              }
            >
              Contact
            </a>
          </li>
        </ul>
      </nav>
    </div>
  );
}

export default NavTabs;